.deck-selector {
  min-height: 100vh;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header {
  text-align: center;
  margin-bottom: 50px;
}

.header h1 {
  color: white;
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 10px;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.header p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.2rem;
  font-weight: 400;
}

.decks-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  max-width: 1200px;
  width: 100%;
  margin-bottom: 50px;
}

.deck-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 15px;
  padding: 25px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.deck-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
}

.deck-card:hover .deck-hover-effect {
  transform: scale(1);
  opacity: 1;
}

.deck-icon {
  font-size: 3rem;
  margin-bottom: 20px;
}

.deck-card h3 {
  color: #333;
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 12px;
}

.deck-card p {
  color: #666;
  font-size: 0.9rem;
  line-height: 1.5;
  margin-bottom: 20px;
}

.deck-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
}

.question-count {
  background: #667eea;
  color: white;
  padding: 6px 12px;
  border-radius: 15px;
  font-weight: 500;
}

.category {
  color: #667eea;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.deck-hover-effect {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%);
  transform: scale(0.8);
  opacity: 0;
  transition: all 0.3s ease;
  pointer-events: none;
}

.footer {
  text-align: center;
}

.footer p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1rem;
  font-weight: 400;
}

@media (max-width: 768px) {
  .deck-selector {
    padding: 20px 10px;
  }
  
  .header h1 {
    font-size: 2rem;
  }
  
  .header p {
    font-size: 1rem;
  }
  
  .decks-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .deck-card {
    padding: 25px 20px;
  }
  
  .deck-card h3 {
    font-size: 1.5rem;
  }
  
  .deck-meta {
    flex-direction: column;
    gap: 10px;
  }
} 